import React, { Component } from 'react';
import {Link} from 'react-router-dom';
import '../Navigation/Navigation.css';
import Resume from '../../Files/Resume_Eric_Porter_1_20_2019_WOD.pdf';
import { library } from '@fortawesome/fontawesome-svg-core';
import { fab } from '@fortawesome/free-brands-svg-icons';
import { fas } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
library.add(fab,fas);

class aboutNav extends Component {
  






  render() {
    const paddlist={'padding-left':'.2vw'}
    const profiles = [
      {to:'https://www.linkedin.com/in/eric-porter-687aa6122/',label:'LinkedIn',icon:'linkedin',icon_pack:'fab'},
      {to:'mailto:eric.porter820@gmail.com' ,label:"Contact",icon:'envelope',icon_pack:'fas'},
     
    ];
    const profile_links = profiles.map(profile => {
              const icon = [profile.icon_pack, profile.icon];

      return (
                <li key ={profile.label}>
                  <a href={profile.to}>
                      {profile.label}  
                      <FontAwesomeIcon icon ={icon} size="lg" style={paddlist}/>
                  </a>
                </li>
        )

    });
    return (

      <div>
        

        <nav>

	        <ul className = "center">
	        <li key="Resume">
              <a href={Resume} download="Eric_Porter.pdf">
                Resume
                <FontAwesomeIcon icon={['fas','file-download']} size="lg" style={paddlist}/>
              </a>
          </li>
	            {profile_links}
	        </ul>
        </nav>
        <h2 className="logo">{this.props.logoTitle}</h2> {/* make this logoTitle "About Me" */}

      </div>
      
    );
  }
}

export default aboutNav;
