import React,{Fragment} from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import * as serviceWorker from './serviceWorker'; 
import { BrowserRouter, Switch, Route} from 'react-router-dom'
import { render } from 'react-dom'
// pages 
import Home from './Pages/Home/Home';
import Musician from './Pages/Music/Music';
import About from './Pages/About/About';
import Designer from './Pages/Designer/Designer';
import EscapeKey from './Pages/EscapeKey/EscapeKey';
import CheckinIn from './Pages/CheckinIn/CheckinIn';





//components
import Navigation from "./Components/Navigation/Navigation"
import Footer from "./Components/Footer/Footer"

// ReactDOM.render(<App />,document.getElementById('root'));
render((  	 
	
		<BrowserRouter >
			<Fragment>
				<Navigation logoTitle="Eric Porter" />
					  		<Switch>
						  		<Route  exact path='/' component={Home}/>
						  		<Route exact path='/Music' component={Musician}/>
						  		<Route exact path='/About' component={About}/>
						  		<Route exact path='/Designer' component={Designer}/>
						  		<Route exact path='/EscapeKey' component={EscapeKey}/>
						  		<Route exact path='/CheckinIn' component={CheckinIn}/>


				</Switch>
				<Footer/>

			</Fragment>
  		</BrowserRouter>
), document.getElementById('root'));

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: http://bit.ly/CRA-PWA
// ReactDom.render(<App />, document.getElementById('root'));
//Render all compnents in app

serviceWorker.unregister();
